.quotes-container {
  display: flex;
  flex-direction: column;
  padding: 40px 50px;
  width: 450px;
  background-color: #fff;
  justify-content: space-between;
  gap: 2rem;
  font-family: "Raleway", sans-serif;
  border-radius: 3px;
  box-shadow: 5px 5px 60px white;
  margin-bottom: 4rem;
}
